/**
 *
 * Table Style - Neat Width (JS)
 *
 * @author Takuto Yanagida
 * @version 2021-01-25
 *
 */


window.NACSS = window['NACSS'] || {};


(function (NS) {

	/**
	 *
	 * Neat Width
	 *
	 * @author Takuto Yanagida
	 * @version 2021-01-25
	 *
	 */
	
	
	function initialize(tabs, opts = {}) {
		if (tabs.length === 0) return;
	
		const lt = tabs[tabs.length - 1];
		const cm = Object.assign({
			fullWidthRate   : 0.95,
			cellMinWidth    : 80,
			cellMinAspect   : 2 / 3,  // width / height
			cellMinLength   : 8,
			maxRowSize      : 200,
			maxBorderWidth  : 2,
			before          : null,  // function (table) { ...; return delay; }
			after           : null,  // function (table) { ... }
			styleNeat       : ':ncTableNeat',
			styleFull       : ':ncTableFull',
			styleScrollRight: ':ncScrollRight',
			styleScrollLeft : ':ncScrollLeft',
		}, opts, getCommonMetrics(lt));
	
		cm.padH += cm.maxBorderWidth * 2;
		cm.padV += cm.maxBorderWidth * 2;
		cm.dcTd = makeDummyCell(lt, 'td');
		cm.dcTh = makeDummyCell(lt, 'th');
	
		const tarTabs = [], noTarTabs = [];
		for (const t of tabs) {
			if (isTarget(t, cm)) tarTabs.push(t);
			else noTarTabs.push(t);
		}
		cm.gcCount = tarTabs.length;
	
		for (const t of tarTabs) {
			const delay = (cm.before) ? (cm.before(t) ?? 0) : false;
			st(() => {
				apply(t, cm);
				enableClass(true, t, cm.styleNeat);
				if (cm.after) cm.after(t);
				if (--cm.gcCount === 0) removeDummyCell(lt, cm);
			}, delay);
		}
		if (cm.fullWidthRate) {
			for (const t of noTarTabs) {
				const pw = t.parentElement.clientWidth;
				if (pw * cm.fullWidthRate < t.clientWidth) enableClass(true, t, cm.styleFull);
			}
		}
		function st(fn, d) { (d === false) ? fn() : setTimeout(fn, d); }
		initScroll(tarTabs, cm);
	}
	
	function getCommonMetrics(tab) {
		const td = tab.getElementsByTagName('td')[0];
		const s = getComputedStyle(td);
		const padH  = parseFloat(s.paddingLeft) + parseFloat(s.paddingRight);
		const padV  = parseFloat(s.paddingTop) + parseFloat(s.paddingBottom);
		const [charW, lineH] = getTextSize(td);
		return { padH, padV, charW, lineH };
	}
	
	function getTextSize(elm) {
		const d = document.createElement(elm.nodeName);
		d.setAttribute('style', `position:fixed;margin:0;padding:0;font-family:${elm.style.fontFamily || 'inherit'};font-size:${elm.style.fontSize || 'inherit'};`);
		d.innerHTML = '\u3000';  // Full width space
		elm.parentNode.appendChild(d);
		const w = d.clientWidth;
		const h = d.clientHeight;
		d.parentNode.removeChild(d);
		return [w, h];
	}
	
	function makeDummyCell(t, tagName) {
		const d = document.createElement(tagName);
		d.setAttribute('style', `position:fixed;display:inline-block;visibility:hidden;white-space:nowrap;`);
		return t.appendChild(d);
	}
	
	function removeDummyCell(lt, cMat) {
		lt.removeChild(cMat.dcTd);
		lt.removeChild(cMat.dcTh);
	}
	
	function isTarget(tab, cMet) {
		if (tab.rows.length === 0) return false;
		if (cMet.maxRowSize < tab.rows.length) return false;
	
		const { cellMinWidth, cellMinAspect } = cMet;
		for (const tr of tab.rows) {
			if (!tr.hasChildNodes()) continue;
			for (const n of tr.childNodes) {
				const tn = n.tagName;
				if (tn !== 'TD' && tn !== 'TH') continue;
				if (1 < parseInt(n.getAttribute('colSpan'), 10)) continue;
				if (1 < parseInt(n.getAttribute('rowSpan'), 10)) continue;
				const cw = n.clientWidth;
				const ch = n.clientHeight;
				if (cw < cellMinWidth || cw / ch < cellMinAspect) return true;
			}
		}
		return false;
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function initScroll(tabs, cMet) {
		const rob = new ResizeObserver(oes => {
			for (const oe of oes) onScroll(oe.target, cMet);
		});
		for (const t of tabs) {
			rob.observe(t);
			t.addEventListener('scroll', throttle(() => { onScroll(t, cMet); }));
		}
	}
	
	function onScroll(tab, cMet) {
		if (tab.scrollWidth - tab.clientWidth > 2) {  // for avoiding needless scrolling
			const r = tab.scrollLeft / (tab.scrollWidth - tab.clientWidth);
			enableClass(r < 0.95, tab, cMet.styleScrollRight);
			enableClass(0.05 < r, tab, cMet.styleScrollLeft);
		} else {
			enableClass(false, tab, cMet.styleScrollRight);
			enableClass(false, tab, cMet.styleScrollLeft);
		}
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function apply(tab, cMet) {
		tab.removeAttribute('width');
		if (tab.style.width) tab.style.width = null;
		if (tab.style.height) tab.style.height = null;
	
		const grid  = makeCellGrid(tab);
		const met   = Object.assign(getMetrics(tab, grid), cMet);
		const newWs = calcNewWidths(grid, met);
		setCellWidth(grid, newWs);
	
		const cs = tab.getElementsByTagName('caption');
		if (cs.length) cs[0].innerHTML = `<span>${cs[0].innerHTML}</span>`;
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function makeCellGrid(t) {
		const css = collectCells(t);
		let maxWidth = 0;
		for (const cs of css) maxWidth = Math.max(maxWidth, cs.length);
		const g = [];
		for (const cs of css) g.push(new Array(maxWidth));
	
		for (let y = 0; y < g.length; y += 1) {
			const gr = g[y];
			const tds = css[y];
			let i = 0;
	
			for (let x = 0; x < maxWidth; x += 1) {
				if (typeof gr[x] === 'number' || gr[x] === null) continue;
	
				const td = tds[i]
				const col = parseInt(td.getAttribute('colSpan') ?? 1, 10);
				const row = parseInt(td.getAttribute('rowSpan') ?? 1, 10);
				gr[x] = td;
	
				if (1 < col) {
					for (let p = 1; p < col; p += 1) gr[x + p] = p;
				}
				if (1 < row) {
					for (let q = 1; q < row; q += 1) {
						const nr = g[y + q];
						for (let p = 0; p < col; p += 1) nr[x + p] = null;
					}
				}
				i += 1;
				if (tds.length <= i) break;
			}
		}
		return g;
	}
	
	function collectCells(t, css = []) {
		for (const tr of t.rows) {
			const cs = [];
			if (tr.hasChildNodes()) {
				for (const n of tr.childNodes) {
					const tn = n.tagName;
					if (tn === 'TD' || tn === 'TH') cs.push(n);
				}
			}
			css.push(cs);
		}
		return css;
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function getMetrics(tab, grid) {
		const origTabW = tab.clientWidth;
		const origCellWs = [];
		for (let x = 0; x < grid[0].length; x += 1) {
			for (let y = 0; y < grid.length; y += 1) {
				const g = grid[y][x];
				if (g instanceof HTMLTableCellElement && !g.getAttribute('colSpan')) {
					origCellWs.push(g.clientWidth);
					break;
				}
			}
		}
		return { origTabW, origCellWs };
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function calcNewWidths(grid, met) {
		for (const gr of grid) {
			for (const gc of gr) {
				if (typeof gc !== 'number' && gc !== null) gc.style.whiteSpace = 'nowrap';
			}
		}
		const gw = grid[0].length;
		const newWs = new Array(gw).fill(0);
		const fixWs = new Array(gw).fill(0);
	
		for (const gr of grid) {
			for (let x = 0; x < gw; x += 1) {
				const td = gr[x];
				if (td === undefined || td === null || typeof td === 'number') continue;
				if (x < gw - 1 && typeof gr[x + 1] === 'number') continue;
				if (1 < parseInt(td.getAttribute('colSpan'), 10)) continue;
				if (1 < parseInt(td.getAttribute('rowSpan'), 10)) continue;
	
				const minW = calcMinWidth(td, met);
				newWs[x] = Math.max(newWs[x], minW);
				if (!minW) fixWs[x] = Math.max(fixWs[x], td.clientWidth);
			}
		}
		widenTabWidth(newWs, fixWs, met);
		return newWs;
	}
	
	function calcMinWidth(td, met) {
		const { padH, padV, charW, lineH, dcTd, dcTh, cellMinWidth, cellMinAspect, cellMinLength } = met;
		if (calcMaxLineLength(td) < cellMinLength) return 0;
	
		td.innerHTML = td.innerHTML.trim();
		const dc = td.tagName === 'TD' ? dcTd : dcTh;
		dc.innerHTML = td.innerHTML;
		const aw = dc.clientWidth - padH;
		const ls = Math.round((dc.clientHeight - padV) / lineH);
		let minW = 0;
		for (let i = 2;; i += 1) {
			const tempW = 0 | (aw / i + charW * i + padH);
			const tempH = ls * (i * lineH) + padV;
			if (tempW < cellMinWidth || tempW / tempH < cellMinAspect || (minW && minW < tempW)) break;
			minW = tempW;
		}
		return (minW + charW < dc.clientWidth) ? minW : 0;
	}
	
	function calcMaxLineLength(td) {
		const ih = td.innerHTML.trim();
		const ls = ih.split(/<\s*br\s*\/?>/ui);
		const ts = ls.map(e => e.replace(/<("[^"]*"|'[^']*'|[^'">])*>/g, '').length);
		return Math.max(...ts);
	}
	
	function widenTabWidth(newWs, fixWs, met) {
		const { origTabW, origCellWs } = met;
		let wNew = 0, wFix = 0;
		for (let i = 0; i < newWs.length; i += 1) {
			if (newWs[i]) {
				wNew += newWs[i];
			} else {
				wFix += fixWs[i];
			}
		}
		if (origTabW < wNew + wFix) return;
		let rem = origTabW - wFix;
		for (let i = 0; i < newWs.length; i += 1) {
			if (!newWs[i]) continue;
			const nw = newWs[i];
			const w = Math.min(nw / wNew * rem, origCellWs[i]);
			rem  -= (w - nw);
			wNew -= (w - nw);
			newWs[i] = 0 | w;
		}
	}
	
	
	// -------------------------------------------------------------------------
	
	
	function setCellWidth(grid, ws) {
		for (const gr of grid) {
			for (let x = 0; x < gr.length; x += 1) {
				const gc = gr[x], w = ws[x];
				if (!w || !(gc instanceof HTMLTableCellElement)) continue;
				gc.style.whiteSpace = null;
				gc.style.minWidth   = w + 'px';
				gc.style.width      = null;
			}
		}
	}
	NS.tableNeatWidth = initialize;

	/**
	 *
	 * Usable View
	 *
	 * @author Takuto Yanagida
	 * @version 2021-01-25
	 *
	 */
	
	
	function initialize(tabs, opts = {}) {
		if (tabs.length === 0) return;
		const cm = Object.assign({
			capableWindowHeightRate: 0.9,
			styleHeaderContainer   : ':ncTableFixedHeaderContainer',
			styleHeaderTable       : ':ncTableFixedHeaderTable',
			styleScrollBar         : ':ncTableFixedScrollBar',
	
			styleFixedHeader       : ':ncFixedHeader',
			styleScrollRight       : ':ncScrollRight',
			styleScrollLeft        : ':ncScrollLeft',
			offset                 : 0,
		}, opts);
	
		const cs = [], ts = [...tabs];
		for (const tab of ts) {
			const head = _createHeaderClone(tab, cm);
			const bar  = _createBarClone(tab, cm);
			cs.push({ tab, head, bar });
	
			let forced = false;
			const el = (tar, op) => throttle(() => {
				if (forced) {
					forced = false;
				} else {
					forced = true;
					op.scrollLeft = tar.scrollLeft;
					if (head) onTableScroll(tab, head, cm);
				}
			});
			tab.addEventListener('scroll', el(tab, bar));
			bar.addEventListener('scroll', el(bar, tab));
		}
		const ro = new ResizeObserver((es) => {
			for (const e of es) {
				const idx = ts.indexOf(e.target);
				const c = cs[idx];
				onResize(e.contentRect, c.tab, c.head, c.bar, cm);
			}
		});
		for (const t of ts) ro.observe(t);
	
		window.addEventListener('scroll', throttle(() => {
			for (const c of cs) onWindowScroll(c.tab, c.head, c.bar, cm);
		}), { passive: true });
	
		const sel = getSelector(cm.styleFixedHeader);
		if (sel) {
			const elm = document.querySelector(sel);
			if (elm) {
				const rob = new ResizeObserver(es => {
					cm.offset = es[0].contentRect.bottom;
					for (const c of cs) onResize(null, c.tab, c.head, c.bar, cm);
				});
				rob.observe(elm);
			}
		}
	}
	
	function _createHeaderClone(tab, cm) {
		let thead = tab.tHead;
		if (!thead) {
			thead = _createPseudoHeader(tab);
			if (!thead) return null;
			tab.tHead = thead;
		}
		const hc = document.createElement('div');
		enableClass(true, hc, cm.styleHeaderContainer);
		tab.parentNode.appendChild(hc);
	
		const ht = document.createElement('div');
		enableClass(true, ht, cm.styleHeaderTable);
		hc.appendChild(ht);
	
		ht.appendChild(thead.cloneNode(true));
		return hc;
	}
	
	function _createPseudoHeader(tab) {
		const trs = tab.tBodies[0].rows;
		if (trs.length === 0) return null;
	
		function containsOnlyTh(tr) {
			const tds = tr.getElementsByTagName('td');
			const ths = tr.getElementsByTagName('th');
			if (tds.length === 0 && ths.length > 0) return true;
			return false;
		}
		const trsH = [];
		for (const tr of trs) {
			if (!containsOnlyTh(tr)) break;
			trsH.push(tr);
		}
		if (trsH.length === 0) return null;
	
		const thead = tab.createTHead();
		for (const tr of trsH) thead.appendChild(tr);
		return thead;
	}
	
	function _createBarClone(tab, cm) {
		const bar = document.createElement('div');
		enableClass(true, bar, cm.styleScrollBar);
		const spacer = document.createElement('div');
		bar.appendChild(spacer);
		tab.parentNode.appendChild(bar);
		return bar;
	}
	
	
	// ---------------------------------------------------------------------
	
	
	function onResize(r, tab, head, bar, cm) {
		tab.style.overflowX = (tab.scrollWidth < tab.clientWidth + 2) ? 'hidden' : null;
	
		if (head) _updateHeaderSize(r, tab, head, cm);
		if (bar) _updateScrollBarSize(tab, bar);
		if (head || bar) onWindowScroll(tab, head, bar, cm);
		if (head) onTableScroll(tab, head, cm);
	}
	
	function _updateHeaderSize(r, tab, head, cm) {
		const tw = r ? r.width : tab.getBoundingClientRect().width;
		head.style.maxWidth = tw + 'px';
		head.style.display  = 'none';
		head.style.top      = cm.offset + 'px';
	
		const thead = tab.tHead;
		const hw = thead.getBoundingClientRect().width;
		const ht = head.firstChild;
		ht.style.width = hw + 'px';
	
		const oTrs = thead.rows;
		const cTrs = ht.firstChild.rows;
		for (let i = 0; i < oTrs.length; i += 1) {
			copyWidth(oTrs[i], cTrs[i], 'td');
			copyWidth(oTrs[i], cTrs[i], 'th');
		}
		function copyWidth(o, c, tag) {
			const os = o.getElementsByTagName(tag);
			const cs = c.getElementsByTagName(tag);
			for (let i = 0; i < os.length; i += 1) {
				cs[i].style.width = os[i].getBoundingClientRect().width + 'px';
			}
		}
	}
	
	function _updateScrollBarSize(tab, bar) {
		const disabled = (tab.scrollWidth < tab.clientWidth + 2);
		bar.style.overflowX     = disabled ? 'hidden' : null;
		bar.style.pointerEvents = disabled ? 'none'   : null;
	
		bar.style.maxWidth = `${tab.clientWidth}px`;
		bar.style.display = 'none';
		const h = parseInt(getScrollBarWidth(document.documentElement));
		if (0 < h) bar.style.height = (h + 2) + 'px';
		bar.firstChild.style.width = `${tab.scrollWidth}px`;
	}
	
	
	// ---------------------------------------------------------------------
	
	
	function onWindowScroll(tab, head, bar, cm) {
		const r = tab.getBoundingClientRect();
		const tBottom = r.bottom;
		const rh = tab.tHead.getBoundingClientRect();
		const hTop = rh.top, hBottom = rh.bottom;
		const wY0 = cm.offset, wY1 = window.innerHeight;
	
		const inView = tBottom - hTop < cm.capableWindowHeightRate * (wY1 - wY0);
		const tLeft = r.left, tScrollLeft = tab.scrollLeft;
		if (head) {
			const hCy = tab.tHead.offsetHeight;
			const f = (!inView && hTop < wY0 && wY0 < tBottom - hCy);
			_updateHeaderVisibility(head, f, tLeft, tScrollLeft);
		}
		if (bar) {
			const f = (!inView && hBottom < wY1 && wY1 < tBottom);
			_updateBarVisibility(bar, f, tLeft, tScrollLeft);
		}
	}
	
	function _updateHeaderVisibility(head, visible, tabLeft, tabScrollLeft) {
		head.style.display = visible ? 'block' : 'none';
		head.style.left    = tabLeft + 'px';
		head.scrollLeft    = tabScrollLeft;
	}
	
	function _updateBarVisibility(bar, visible, tabLeft, tabScrollLeft) {
		bar.style.display = visible ? 'block' : 'none';
		bar.style.left    = tabLeft + 'px';
		bar.scrollLeft    = tabScrollLeft;
	}
	
	
	// ---------------------------------------------------------------------
	
	
	function onTableScroll(tab, head, cm) {
		const sL = Math.max(0, Math.min(tab.scrollLeft, tab.scrollWidth - tab.offsetWidth));  // for iOS
		head.scrollLeft = sL;
	
		if (tab.scrollWidth - tab.clientWidth > 2) {  // for avoiding needless scrolling
			const r = tab.scrollLeft / (tab.scrollWidth - tab.clientWidth);
			enableClass(r < 0.95, head, cm.styleScrollRight);
			enableClass(0.05 < r, head, cm.styleScrollLeft);
		} else {
			enableClass(false, head, cm.styleScrollRight);
			enableClass(false, head, cm.styleScrollLeft);
		}
	}
	
	
	// Utilities ---------------------------------------------------------------
	
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	
	function getScrollBarWidth(parent) {
		const d = document.createElement('div');
		d.setAttribute('style', 'position:absolute;bottom:100%;width:calc(100vw - 100%);height:1px;');
		parent.appendChild(d);
		let width = 0 | window.getComputedStyle(d).getPropertyValue('width');
	
		if (width === 0) {  // Window does not have any scroll bar
			d.style.overflowY = 'scroll';
			d.style.width = '';
			const c = document.createElement('div');
			c.style.minHeight = '100px';
			d.appendChild(c);
			const cw = 0 | window.getComputedStyle(c).getPropertyValue('width');
			width = d.offsetWidth - cw;
		}
		parent.removeChild(d);
		return width;
	}

})(window.NACSS);
